import { FormErrors } from '../errors/validation.error';
import { DepartmentForm } from '../forms/department.forms';
import { createValidator, ValidationRules } from '../utils/validation.utils';

export const validateDepartmentForm = (form: DepartmentForm): FormErrors<DepartmentForm> => {
    const errors: FormErrors<DepartmentForm> = {};

    errors.name = createValidator([
        { check: (v) => ValidationRules.required(v), message: 'Department name is required' },
        { check: (v) => ValidationRules.minLength(v, 2), message: 'Department name must be at least 2 characters' },
        { check: (v) => ValidationRules.maxLength(v, 50), message: 'Department name must be less than 50 characters' }
    ])(form.name);

    errors.description = createValidator([
        { check: (v) => !v || ValidationRules.minLength(v, 5), message: 'Description must be at least 5 characters' },
        { check: (v) => !v || ValidationRules.maxLength(v, 500), message: 'Description must be less than 500 characters' }
    ])(form.description);

    errors.employees = null;

    return errors;
};
